@font-face {
  font-family: "Tilda Script";
  src: url("./assets/fonts/TildaScript/TildaScript-Regular.eot"); /* IE9*/
  src: url("./assets/fonts/TildaScript/TildaScript-Regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("./assets/fonts/TildaScript/TildaScript-Regular.woff2")
      format("woff2"),
    /* chrome、firefox */ url("./assets/fonts/TildaScript/TildaScript-Regular.woff")
      format("woff"),
    /* chrome、firefox */ url("./assets/fonts/TildaScript/TildaScript-Regular.ttf")
      format("truetype"),
    /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
      url("./assets/fonts/TildaScript/TildaScript-Regular.svg#Comic Sans MS") format("svg"); /* iOS 4.1- */
}

@font-face {
  font-family: "Housegrind";
  src: url("./assets/fonts/Housegrind/HousegrindPersonalUseOnly.eot"); /* IE9*/
  src: url("./assets/fonts/Housegrind/HousegrindPersonalUseOnly.eot?#iefix")
      format("embedded-opentype"),
    /* chrome、firefox */ url("./assets/fonts/Housegrind/HousegrindPersonalUseOnly.woff")
      format("woff"),
    /* chrome、firefox */ url("./assets/fonts/Housegrind/HousegrindPersonalUseOnly.ttf")
      format("truetype")
}

@font-face {
  font-family: "Arioso";
  src: url("./assets/fonts/Arioso/ARIR.eot"); /* IE9*/
  src: url("./assets/fonts/Arioso/ARIR.eot?#iefix")
      format("embedded-opentype"),
    /* chrome、firefox */ url("./assets/fonts/Arioso/ARIR.woff")
      format("woff"),
    /* chrome、firefox */ url("./assets/fonts/Arioso/ARIR.ttf")
      format("truetype")
}

body {
  font-family: "Arioso";
  height: 70em;
  background: rgb(148, 228, 252);
  background: linear-gradient(0deg, rgba(148,228,252,1) 0%, rgba(255,255,255,1) 50%, rgba(255,255,255,1) 67%, rgba(215,173,137,1) 100%);
}
